<template>
    <el-row style="margin-top: 40px;">
        <el-col :span="5" :offset="2">
            <!-- 个人信息 -->
            <el-card class="profile-card" shadow="always">
                <div class="background">
                </div>
                <div class="content">
                    <el-row>
                        <el-col :span="6" :offset="9">
                                <div class="photo">
                                    <img :src="$store.state.user.photo" alt="" class="" style="user-select: none;">
                                </div>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="20" :offset="2">
                            <div class="username">
                                {{ $store.state.user.username }}
                            </div>

                            <div class="profession">
                                Web Developer at Webestica
                            </div>

                            <div class="description">
                                I'd love to change the world, but they won’t give me the source code.
                            </div>

                            <div class="friend-num-container">
                                <div>
                                    <h6 class="num">256</h6>
                                    <small class="text">Post</small>
                                </div>
                                <div class="vertical-divider"></div>
                                <div>
                                    <h6 class="num">2.5K</h6>
                                    <small class="text">Followers</small>
                                </div>
                                <div class="vertical-divider"></div>
                                <div>
                                    <h6 class="num">365</h6>
                                    <small class="text">Records</small>
                                </div>
                            </div>

                            <div class="horizontal-divider"></div>
                        </el-col>
                    </el-row>
                </div>
            </el-card>
        </el-col>
        <el-col :span="14" :offset="1">
            <el-tabs class="tabs" type="border-card">
                <el-tab-pane label="我的Bots">
                    <UserBotIndexView />
                </el-tab-pane>
                <el-tab-pane label="我的录像">
                    <el-card style="margin-top: 25px; user-select: none;">
                        <el-table :data="records" style="width: 100%;" highlight-current-row max-height="720" table-layout="auto">
                            <el-table-column prop="createtime" align="center" label="对局时间" />
                            <el-table-column prop="game" label="游戏" align="center" width="190" />
                            <el-table-column label="玩家" align="center" width="250">
                                <template #default="scope">
                                    <div class="player-container">
                                        <div class="player-card">
                                            <el-avatar shape="square" size="small" :src="scope.row.a_photo" />
                                            <span class="player-card-username">
                                                <el-link type="primary" style="user-select: text;">
                                                    <span class="records-username">
                                                        {{ scope.row.a_username }}
                                                    </span>
                                                </el-link>
                                            </span>
                                            <span class="player-result" v-if="scope.row.result === 'B'" style="color: #0099CC;">
                                                +5
                                            </span>
                                            <span class="player-result" v-else-if="scope.row.result === 'A'" style="color: #FF0000;">
                                                -2
                                            </span>
                                            <span class="player-result" v-else-if="scope.row.result === 'all'" style="color: #0099CC;">
                                                +0
                                            </span>
                                        </div>
                                        <span class="player-desb">
                                            VS
                                        </span>
                                        <div class="player-card">
                                            <el-avatar shape="square" size="small" :src="scope.row.b_photo" />
                                            <span class="player-card-username">
                                                <el-link type="primary" style="user-select: text;">
                                                    <span class="records-username">
                                                        {{ scope.row.b_username }}
                                                    </span>
                                                </el-link>
                                            </span>
                                            <span class="player-result" v-if="scope.row.result === 'B'" style="color: #ff0000;">
                                                -2
                                            </span>
                                            <span class="player-result" v-else-if="scope.row.result === 'A'" style="color: #0099CC;">
                                                +5
                                            </span>
                                            <span class="player-result" v-else-if="scope.row.result === 'all'" style="color: #0099CC;">
                                                +0
                                            </span>
                                        </div>
                                    </div>
                                </template>
                            </el-table-column>
                            
                            <el-table-column fixed="right" label="Operations"  align="center">
                                <template #default="scope">
                                    <el-button @click="see_record(scope.row.id)" color="#6666CC">查看录像</el-button>
                                </template>
                            </el-table-column>
                        </el-table>
                    </el-card>
                </el-tab-pane>
                <el-tab-pane label="我的动态">
                    有什么新鲜事想告诉大家！快去发帖吧！
                </el-tab-pane>
                <el-tab-pane label="我的发帖">
                    快去讨论讨论游戏叭！
                </el-tab-pane>
            </el-tabs>
        </el-col>
    </el-row>
</template>

<script>
import UserBotIndexView from '@/components/UserBotIndexView.vue';
import $ from 'jquery';
import { ref, onMounted } from 'vue';
import { useStore } from 'vuex';
import router from '@/router';

export default {
    name: 'SpaceMainView',
    components: {
        UserBotIndexView
    },
    setup() {
        const store = useStore();
        let records = ref([]);
        let total_records = ref(0);

        const pull_records = page => {
            $.ajax({
                url: "https://aigame.zzqahm.top/backend/record/getlist/user/",
                type: "get",
                data: {
                    page,
                },
                headers: {
                    "Authorization": "Bearer " + store.state.user.access,
                },
                success(resp) {
                    if(resp.result === "success") {
                        records.value = JSON.parse(resp.records);
                        total_records.value = resp.records_count;
                    }
                },
            });
        };

        const stringTo2D = map => {
            let g = [];
            for(let i = 0, k = 0; i < 13; i++) {
                let line = [];
                for(let j = 0; j < 14; j++, k++) {
                    if(map[k] === '0') line.push(0);
                    else line.push(1);
                }
                g.push(line);
            }
            return g;
        }

        onMounted(() => {
            // 翻页也得调用
            pull_records(1);
        });

        const see_record = recordId => {
            for(const record of records.value) {
                if(record.id === recordId) {
                    store.commit("updateIsRecord", true);
                    if(record.game_id === 2) {
                        store.commit("updateSnakeGame", {
                            map: stringTo2D(record.map),
                            a_id: record.a_id,
                            b_id: record.b_id,
                        });
                    } else if(record.game_id === 1 || record.game_id === 3) {
                        // 当加上随机优先后，可以带firstMove参数
                        store.commit("updateChessGame", {
                            a_id: record.a_id,
                            b_id: record.b_id,
                        });
                    }
                    store.commit("updateSteps", {
                        a_steps: record.a_steps,
                        b_steps: record.b_steps,
                    });
                    store.commit("updateRecordLoser", record.result);
                    store.commit("updatePlayerInfo", {
                        a_username: record.a_username,
                        a_photo: record.a_photo,
                        a_language: record.a_language,
                        b_username: record.b_username,
                        b_photo: record.b_photo,
                        b_language: record.b_language,
                    });
                    store.commit("updateIsRobot", {
                        a_is_robot: record.a_is_robot,
                        b_is_robot: record.b_is_robot
                    });
                    store.commit("updateBackPage", "record_index");
                    router.push({
                        name: 'record_content',
                        params: {
                            recordId,
                        },
                        query: {
                            game: record.game_id,
                        }
                    });
                    break;
                }
            }
        };

        return {
            records,
            see_record,
            total_records,
            pull_records,
        }
    }
}
</script>

<style scoped>
.el-card:deep(.el-card__body)  {
    padding: 0 0 !important;
}

.profile-card {
    border: none !important;
}
    
.profile-card .background {
    background-image: url('@/assets/images/01.jpg');
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    height: 50px;
}

.profile-card .photo > img {
    margin-top: -40px;
    border-radius: 10px 10px 0 0;
    border: 3px solid white;
    width: 100%;
    height: 100%;
}

.profile-card .username {
    text-align: center;
    font-size: 20px;
    font-weight: 700;
    height: 20px;
    line-height: 20px;
    margin-top: 18px;
    user-select: text;
}

.profile-card .profession {
    margin-top: 5px;
    color: #676A79;
    font-size: 12px;
    text-align: center;
}

.profile-card .description {
    color: #676A79;
    font-size: 13px;
    text-align: center;
    width: 90%;
    margin: 15px auto;
}

.profile-card .friend-num-container {
    display: flex;
    justify-content: space-between;
}

.profile-card .friend-num-container div:nth-child(1) {
    margin-left: 10px;
}

.profile-card .friend-num-container div:last-child {
    margin-right: 10px;
}

.profile-card .friend-num-container .num {
    text-align: center;
    height: 12px !important;
    line-height: 12px !important;
    margin-bottom: 0px;
}

.profile-card .friend-num-container .text {
    font-size: 8px;
    color: #676A79;
    user-select: none;
}

.profile-card .vertical-divider {
    height: 40px;
    border-left: 1px solid #CCCCCC;
    margin: 0 5px;
    padding: 8px 0;
}

.profile-card .horizontal-divider {
    width: 100%;
    border-top: 1px solid #CCCCCC;
    margin: 10px 0px;
    padding: 0 10px;
    margin-bottom: 15px;
}

.player-container {
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.player-card {
    border: 1px solid #ccc;
    border-radius: 5px;
    height: 50px;
    width: 90px;
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.player-desb {
    color: #FF0033;
    font-size: 12px;
}

.player-result {
    background-color: #CCCCCC;
    width: 25px;
    height: 17px;
    text-align: center;
    font-size: 13px;
    line-height: 17px;
}

.records-username {
    user-select: text;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    width: 35px;
}
</style>